body {
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-style: normal;
}
.App {
  font-family: sans-serif;
  text-align: center;
}
.logo {
  width: 160px;
  height: auto;
}
.trand-right-single .trand-right-cap h4 {
  font-size: 18px;
}
.trand-right-single .trand-right-cap h4 a {
  font-weight: 500;
  line-height: 1.4;
}
.trand-right-single .trand-right-img img {
  border-radius: 6px;
}
.trand-right-single .trand-right-cap {
  padding-left: 18px;
}
.trand-right-single .trand-right-cap span {
  color: #000;
  text-transform: uppercase;
  font-size: 11px;
  font-weight: 400;
  padding: 10px 15px;
  line-height: 1;
  margin-bottom: 15px;
  display: inline-block;
}
.trending-area .trending-main .trand-right-single {
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #eee;
}
.color {
  background: #e1fcff;
}
.nav.navbar-nav li {
  font-size: 32px;
  padding: 0px 5px;
}
.Topmenu .navbar {
  margin-left: 0px;
}
.trending-title {
  margin-top: 25px;
}
.footer h3 {
  text-align: center;
  margin-top: 50px;
}
